import React from 'react';
import {BrowserRouter, Route, Routes} from 'react-router-dom';
import Home from './routes/Home';
import Aline from './routes/Aline';
import Scent from './routes/Scent';
import About from './routes/About';
import Athena from './routes/Athena';
import Flot from './routes/Flot';
import Hugo from './routes/Hugo';
import StickIt from './routes/StickIt';
import ScrollToTop from './components/ScrollToTop';
import Navigation from './components/Navigation';
import Ringle from './routes/Ringle'

function App(){


  return <BrowserRouter>
    <ScrollToTop />
    <Navigation/>
    <Routes>
      <Route path='/' exact={true} element= {<Home/>} />
      <Route path='/about' element= {<About/>} />
      <Route path='/aline' element= {<Aline/>} />
      <Route path='/athenahealth' element= {<Athena/>} />
      <Route path='/ringle' element= {<Ringle/>} />
      <Route path='/scent' element= {<Scent/>} />
      <Route path='/flot'  element= {<Flot/>} />
      <Route path='/hugo'  element= {<Hugo/>} />
      <Route path='/stick-it' element = {<StickIt/>} />
    </Routes>
  </BrowserRouter>
}

export default App;