import React from 'react'
import { Link, useLocation } from 'react-router-dom';
import "./Navigation.css";
import Resume from '../document/Resume.pdf';



function Navigation(){
    let location = useLocation();




    return (
    <div className="nav">
        {location.pathname === '/' || location.pathname === '/about' ? 
            <div className='topnav'><div className='grid-container-nav'>
                <Link to='/'><div className='text-name-sooyeon'>Sue Park</div></Link>
                <div className='taps'>
                    {location.pathname === '/' ?
                    <Link to='/'><div className='text-name-tap-underline'>WORK</div></Link> :
                    <Link to='/'><div className='text-name-tap'>WORK</div></Link>
                    }
                    {location.pathname === '/about' ?
                    <Link to='/about'><div className='text-name-tap-underline'>ABOUT</div></Link> :
                    <Link to='/about'><div className='text-name-tap'>ABOUT</div></Link>
                    }
                    <a href = {Resume} target = "_blank" rel="noopener noreferrer">RESUME</a>
                </div>
                </div>
            </div> 
            : <Link to='/' className='arrow'>&#8592;</Link>}
    </div>);
}

export default Navigation;